import SearchCom from '@/pages/commonCom/SearchCom.vue';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import skeleton from '@/components/xinyi-skeleton/skeleton.vue';
export default {
	data() {
		return {
			loadingPage: true,
			skeletonData:[1,2,3,4,5],
			loadingstatus: 'more',
			loadingText: '加载中...',
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2--contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			hasData:false,
			showSearch:false,
			data:[],
			httpQuery: {
				pageNo: 1,
				pageSize: 10,
				ernam: '',
				bizrk: '',
				// group: '',
				orderStart: '',
				orderEnd: '',
				category: 1, //1主机，2配件
				queryType: 1, // 1所有， 2我的订单
				type: 1 // 1所有 2未发 3已发 4待收
			},
			evePageLen: "", //后台每页返回数据的长度，用来判断是否需要请求更多
			searchData: [
				{
					queryName: 'ernam',
					type: 'text',
					placeholder: '请输入创建人',
					value: '',
				},
				{
					queryName: 'bizrk',
					type: 'select',
					valueName: '请选择销售地区',
					value: '',
					index: 0,
					options: getApp().globalData.sales_area_options
				},
				// {
				// 	queryName: 'group',
				// 	type: 'text',
				// 	placeholder: '请输入产品组',
				// 	value: '',
				// },
				{
					queryName: 'orderStart',
					type: 'date',
					placeholder: '请选择下单开始日期',
					value: '',
				},
				{
					queryName: 'orderEnd',
					type: 'date',
					placeholder: '请选择下单结束日期',
					value: '',
				}
			],
			requestUrl:'app/home/order/page'
		}
	},
	onLoad() {
		var self = this
		this.loadData()
	},
	components: {
		SearchCom,
		uniLoadMore,
		skeleton
	},
	methods: {
		initHttpQuery() {
			for(var k in this.httpQuery) {
				this.httpQuery[k] = ''
			}
			this.httpQuery.pageSize = 10
			this.httpQuery.pageNo = 1
			this.httpQuery.category = 1 // 1主机， 2配件
			this.httpQuery.queryType = 1 //1所有， 2我的订单 
			this.httpQuery.type = 1 // 1所有 2未发 3已发 4待收
			this.searchData.forEach(item => { item.value = '' })
			console.log(this.httpQuery)
		},
		onPullDownRefresh() {
			this.initHttpQuery()
			this.loadingPage = true
			this.loadData()
		},
		onReachBottom() {
			//触底的时候请求数据，即为上拉加载更多
			this.getmore()
		},
		loadData() {
			//下拉刷新
			let self = this;
			//第一次回去数据
			this.httpQuery.pageNo = 1;
			this.loadingType = 0;
			uni.showNavigationBarLoading();
			//请求接口获取数据
			this.httpGetListData()
		},
		getmore() {
			let self = this;
			if (this.loadingType !== 0 && this.evePageLen < 10) {
				//loadingType!=0;直接返回
				this.loadingType = 2;
				this.loadingstatus = 'noMore';
				return false;
			}
			this.loadingType = 1;
			this.loadingstatus = 'loading';
			uni.showNavigationBarLoading(); //显示加载动画
			this.httpGetMoreListData()
		},
		httpGetListData() {
			uni.hideLoading()
			var self = this
			// 数据请求
			this.ajax({
				url:this.requestUrl,
				data: this.httpQuery,
				success:res=>{
					console.log(res)
					this.loadingPage = false
					var data = res.data
					this.getListSuccess(data)
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getListSuccess(data) {
			//加载数据成功函数，处理数据
			if (data.length != 0) {
				this.hasData = true
			}else {
				this.hasData = false
			}
			this.evePageLen = data.length
			data.forEach(item => {
				for(let k in item) {
					if (item[k] == null) { item[k] = "" }
				}
			})
			this.data = data
			if (this.evePageLen < 10) {
				this.loadingType = 2;
				this.loadingstatus = 'noMore';
			}
			uni.hideNavigationBarLoading()
			uni.stopPullDownRefresh()
		},
		httpGetMoreListData() {
			var self = this
			this.httpQuery.pageNo++
			//数据更请求
			this.ajax({
				url:this.requestUrl,
				data: this.httpQuery,
				success:res=>{
					console.log(res)
					var data = res.data
					this.getMoreListSuccess(data)
				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getMoreListSuccess(data) {
			data.forEach(item => {
				for(let k in item) {
					if (item[k] == null) { item[k] = "" }
				}
			})
			this.data = this.data.concat(data)
			this.evePageLen = data.length
			this.loadingType = 0; //将loadingType归0重置
			this.loadingstatus = 'loading'
			if (this.evePageLen == 0 || this.evePageLen < 10) {
				this.loadingType = 2
				this.loadingstatus = 'noMore'
			}
			uni.hideNavigationBarLoading() //关闭加载动画
			uni.stopPullDownRefresh()
		},
		openCloseSearch() {
			var self = this
			this.showSearch = !this.showSearch
			if (this.showSearch == true) {
				setTimeout(() => {
					self.$refs.searchs.getInputtoBtn()
				}, 200)
			}
		},
		searchConfirm(searchData, updateData) {
			for(var k in searchData) {
				for(var key in this.httpQuery) {
					if (k == key) { this.httpQuery[key] = searchData[k] }
				}
			}
			this.searchData = updateData
			console.log(this.httpQuery)
			this.openCloseSearch()
			this.loadingPage = true
			this.data = []
			this.loadData()
		},
		gotoDeatail(item) {
			var itemData = JSON.parse(JSON.stringify(item))
			delete itemData.imgUrl
			uni.navigateTo({
			    url: '/host/order/detail?data='+encodeURI(JSON.stringify(itemData))
			})
		}
	},
	filters:{
		statusFilter(val) {
			const statusMap = {
			    1: '生产中'
			}
			return statusMap[val]
		}
	}
}
